import React from 'react';
import styles from './style.module.sass';

const ProductCard : React.FC = () => {
    return (
        <div className={styles.wrapper}>
            <img src="/images/product1.png" alt=""/>
            <div className={styles.info}>
                <span className={styles.label}>Mila Bezgerts</span>
                <span className={styles.name}>Жилет</span>
                <span className={styles.params}>44-164, 46-164, 48-1asdf</span>
                <span className={styles.price}>1 578 ₽</span>
            </div>
            <div className={styles.rating}>
                <div className={styles['rating-upper']} style={{width: '50%'}}>
                    <span>★</span>
                    <span>★</span>
                    <span>★</span>
                    <span>★</span>
                    <span>★</span>
                </div>
                <div className={styles['rating-lower']}>
                    <span>★</span>
                    <span>★</span>
                    <span>★</span>
                    <span>★</span>
                    <span>★</span>
                </div>
            </div>
        </div>
    );
}


export default ProductCard;